import { lego } from '@armathai/lego';
import { hintParamGuard } from '../../../guards/ad/hint-param-guard';
import { timerParamGuard } from '../../../guards/timer/timer-param-guard';
import { initializeTimerModelCommand } from '../../timer/initialize-timer-model-command';
import { startTimerCommand } from '../../timer/start-timer-command';
import { initializeHintModelCommand } from '../hint/initialize-hint-model-command';
import { startHintVisibilityTimerCommand } from '../hint/start-hint-visibility-timer-command';
import { destroyTutorialModelCommand } from './destroy-tutorial-model-command';

export function onTutorialCompleteCommand() {
  lego.command
    //
    .execute(destroyTutorialModelCommand)

    .guard(timerParamGuard)
    .execute(initializeTimerModelCommand, startTimerCommand)

    .guard(hintParamGuard)
    .execute(initializeHintModelCommand, startHintVisibilityTimerCommand);
}
