/* eslint-disable class-methods-use-this */
import { getCTAGridConfig } from '../../configs/grid-configs';
import { postRunnable, showFromBottom } from '../../utils';
import { CTAEmbeddedPopup } from './cta-embedded-popup';
import { CTAView } from './cta-view';

export class CTAEmbeddedView extends CTAView {
  constructor() {
    super();

    this._first = true;
    this._build();
  }

  getGridConfig() {
    return getCTAGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    if (this._popup && !this._first) {
      this._popup.destroy();
      this._buildPopup();
      this._popup.alpha = 1;
      this._popup.buildHint();
    }

    super.rebuild(config);
  }

  show() {
    const tw = showFromBottom(this._popup, false);
    postRunnable(() => tw.start());

    tw.onStart.add(() => {
      this._popup.alpha = 1;
    });

    tw.onComplete.add(() => {
      this._popup.buildHint();
      this._first = false;
    });
  }

  _build() {
    this.build(this.getGridConfig());
    this._buildPopup();
  }

  _buildPopup() {
    const popup = new CTAEmbeddedPopup();
    this.setChild('popup', (this._popup = popup));
  }
}
