/* eslint-disable class-methods-use-this */
import { getCtaHeaderTextConfig } from '../../configs/text-configs';
import { fitText, makeText } from '../../utils';
import { Container } from '../../utils/container';
import { CTAHint } from './cta-hint';
import { EmbeddedItem } from './embedded-item';

function getPopupConfig() {
  const width = LP(530, 550);
  const radius = LP(28, 25);

  return {
    bg: {
      width,
      height: LP(530, 550),
      radius,
      color: 0x1a40a5
    },
    header: {
      width,
      height: LP(150, 160),
      radius,
      color: 0x2964fe
    },
    headerCover: {
      width,
      height: radius,
      color: 0x1a40a5
    },
    itemBg: {
      width: LP(190, 200),
      height: LP(350, 360),
      radius: 15,
      color: 0x1a40af
    }
  };
}

function getItemConfig() {
  return {
    left: {
      x: LP(30, 30),
      y: LP(150, 165),
      name: 'firefighters'
    },
    right: {
      x: LP(280, 290),
      y: LP(150, 165),
      name: 'firetruck'
    }
  };
}

export class CTAEmbeddedPopup extends Container {
  constructor() {
    super();

    this._build();
  }

  get name() {
    return 'CTAEmbeddedPopup';
  }

  getBounds() {
    return this._bg.getBounds();
  }

  getHintPosition() {
    const { bottom: yL, centerX: xL } = this._leftItem;
    const { bottom: yR, centerX: xR } = this._rightItem;
    const point1 = new Phaser.Point(xL, yL);
    const point2 = new Phaser.Point(xR, yR);
    return [point1, point2];
  }

  buildHint() {
    const hint = new CTAHint();
    this.addChild((this._hint = hint));
  }

  _build() {
    const { bg, header, headerCover } = getPopupConfig();

    this._buildBg(bg);
    this._buildHeader(header);
    this._buildHeaderText();
    this._buildHeaderCover(headerCover);
    this.addChild((this._leftItem = this._buildItem('left')));
    this.addChild((this._rightItem = this._buildItem('right')));

    this.alpha = 0;
  }

  _buildBg(config) {
    const { width, height, color, radius } = config;
    const bg = this._getRoundedRect(width, height, radius, color);

    this.addChild((this._bg = bg));
  }

  _buildHeader(config) {
    const { width, height, color, radius } = config;
    const headerGr = this._getRoundedRect(width, height, radius, color);

    this.addChild((this._header = headerGr));
  }

  _buildHeaderText() {
    const text = makeText(getCtaHeaderTextConfig());
    fitText(text, this._header.width * 0.9, this._header.height);

    text.centerX = this._header.centerX;
    text.centerY = this._header.centerY - 10;
    this._header.addChild(text);
  }

  _buildHeaderCover(config) {
    const { width, height, color } = config;
    const cover = this._getRect(width, height, color);
    cover.centerY = this._header.bottom - 7;

    this.addChild((this._headerCover = cover));
  }

  _buildItem(side) {
    const { x, y, name } = getItemConfig()[side];
    const item = new EmbeddedItem(side, name);
    item.position.set(x, y);
    return item;
  }

  _getRoundedRect(width, height, radius, color) {
    const gr = this.game.add.graphics();
    gr.beginFill(color, 1);
    gr.drawRoundedRect(0, 0, width, height, radius);
    gr.endFill();

    return gr;
  }

  _getRect(width, height, color) {
    const gr = this.game.add.graphics();
    gr.beginFill(color, 1);
    gr.drawRect(0, 0, width, height);
    gr.endFill();

    return gr;
  }
}
