/* eslint-disable class-methods-use-this */
import { loopRunnable, removeRunnable } from '../../../utils';
import { Container } from '../../../utils/container';

export class FiretruckTimerComponent extends Container {
  constructor(duration) {
    super();

    this._duration = duration;
    this._remaining = duration;
    this._delta = CI_API.game.time.physicsElapsedMS;

    this._build();
    this._start();
  }

  get name() {
    return 'FiretruckTimerComponent';
  }

  _progressUpdate(progress) {
    this._updateGrWidth(this._maskRect, progress);
  }

  _updateGrWidth(gr, progress) {
    const prog = Math.max(progress.toFixed(3), 0);
    const angle = -2 * Math.PI * (1 - prog);

    gr.clear();
    gr.beginFill(0xff0000);

    if (prog > 0) {
      gr.arc(0, 0, 40, angle - Math.PI * 0.5, -Math.PI * 0.5, true);
    } else {
      this.alpha = 0;
    }
  }

  _start() {
    const runnable = loopRunnable(this._delta, () => {
      this._remaining -= this._delta;

      if (this._remaining <= 0) {
        removeRunnable(runnable);
        this.destroy();
      } else {
        this._progressUpdate(this._remaining / this._duration);
      }
    });
  }

  _build() {
    this._buildInnerCircle();
    this._buildFill();
    this._buildMask();
  }

  _buildInnerCircle() {
    this._bg = this.game.add.graphics();
    this._bg.beginFill(0x000000, 0.7);
    this._bg.drawCircle(0, 0, 40);
    this._bg.endFill();
    this.addChild(this._bg);
  }

  _buildFill() {
    this._fill = this.game.add.graphics();
    this._fill.lineStyle(5, 0x00ff67, 1);
    this._fill.drawCircle(0, 0, 25);
    this._fill.endFill();
    this.addChild(this._fill);
  }

  _buildMask() {
    const gr = this.game.add.graphics();
    gr.beginFill(0x00ff00);
    gr.drawCircle(0, 0, 60);
    this.addChild((this._maskRect = gr));

    this._fill.mask = gr;
  }
}
