/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getCashIconImageConfig, getCTAIconImageConfig } from '../../configs/image-configs';
import { getCtaItemPriceTextConfig, getCtaItemTextConfig } from '../../configs/text-configs';
import { InputPriority } from '../../constants';
import { ViewEvents } from '../../events/view-events';
import { fitText, makeImage, makeText } from '../../utils';
import { Container } from '../../utils/container';

function itemConfig() {
  const width = 230;
  const height = 350;
  const radius = 15;

  return {
    bg: {
      width,
      height,
      radius,
      color: 0x1b306f
    },
    imgBg: {
      width,
      height: 250,
      radius,
      color: 0xffffff
    },
    imgBgCover: {
      width,
      height: 20,
      color: 0x1b306f
    }
  };
}

function getTextConfig() {
  return {
    left: {
      price: '$600',
      name: 'Firefighters'
    },
    right: {
      price: '$1000',
      name: 'Firetruck'
    }
  };
}

export class EmbeddedItem extends Container {
  constructor(side, name) {
    super();

    this._build(side, name);
  }

  _build(side, name) {
    const { bg, imgBg, imgBgCover } = itemConfig();
    this._buildBg(bg);
    this._buildImgBg(imgBg);
    this._buildImg(side);
    this._buildImgBgCover(imgBgCover);
    this._buildText(side);

    this._buildHitArea(name);
  }

  _buildBg(config) {
    const { width, height, color, radius } = config;
    const bg = this._getRoundedRect(width, height, radius, color);

    this.addChild((this._bg = bg));
  }

  _buildImgBg(config) {
    const { width, height, color, radius } = config;
    const bg = this._getRoundedRect(width, height, radius, color);

    this.addChild((this._imgBg = bg));
  }

  _buildImgBgCover(config) {
    const { width, height, color } = config;
    const cover = this._getRect(width, height, color);
    cover.centerY = this._imgBg.bottom;

    this.addChild((this._imgBgCover = cover));
  }

  _buildImg(side) {
    const img = makeImage(getCTAIconImageConfig(side));
    img.centerX = this._imgBg.centerX;
    img.centerY = this._imgBg.centerY - 5;

    this._imgBg.addChild(img);
  }

  _buildText(side) {
    const y1 = 270;
    const y2 = 320;
    const { name: nameC, price: priceC } = getTextConfig()[side];
    const name = makeText(getCtaItemTextConfig(nameC));
    const price = makeText(getCtaItemPriceTextConfig(priceC));
    const moneyIcon = makeImage(getCashIconImageConfig());

    name.anchor.set(0.5);
    name.centerY = y2;
    name.centerX = this._bg.centerX;
    fitText(name, this._bg.width * 0.9, this._bg.height - name.y);

    price.anchor.set(0, 0.5);
    price.left = this._bg.centerX - 10;
    price.centerY = y1;
    fitText(price, this._bg.width * 0.5, 100);

    moneyIcon.scale.set(0.8);
    moneyIcon.centerY = y1;
    moneyIcon.right = this._bg.centerX - 20;

    this.addChild(name);
    this.addChild(price);
    this.addChild(moneyIcon);
  }

  _getRoundedRect(width, height, radius, color) {
    const gr = this.game.add.graphics();
    gr.beginFill(color, 1);
    gr.drawRoundedRect(0, 0, width, height, radius);
    gr.endFill();

    return gr;
  }

  _getRect(width, height, color) {
    const gr = this.game.add.graphics();
    gr.beginFill(color, 1);
    gr.drawRect(0, 0, width, height);
    gr.endFill();

    return gr;
  }

  _buildHitArea(name) {
    const { width, height } = this._bg;

    const gr = this.game.add.graphics();
    gr.beginFill(0xff0000, 0);
    gr.drawRect(0, 0, width, height);
    gr.endFill();

    gr.name = name;
    gr.inputEnabled = true;
    gr.input.priorityID = InputPriority.Cta;

    gr.events.onInputDown.add(() => {
      lego.event.emit(ViewEvents.CtaView.PlayClick);
    });

    this.addChild((this._hitArea = gr));
  }
}
