import { lego } from '@armathai/lego';
import { AdStatus, InputPriority } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { searchAtlasByFrame } from '../../utils';

export class LogoView extends Phaser.Sprite {
  constructor() {
    const texture = searchAtlasByFrame('ui/logo.png');
    super(CI_API.game, 0, 0, texture.key, texture.frame);
    this.name = 'logo';
    this.inputEnabled = true;
    this.input.priorityID = InputPriority.Foreground;

    lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.Cta:
        this.inputEnabled = false;
        break;
      default:
        break;
    }
  }
}
