/* eslint-disable class-methods-use-this */
import { getViewByProperty, handMoveTw, handScaleTw, searchAtlasByFrame } from '../../utils';

export class CTAHint extends Phaser.Sprite {
  constructor() {
    const texture = searchAtlasByFrame('ui/hint.png');
    super(CI_API.game, 0, 0, texture.key, texture.frame);
    this.anchor.set(0.5, 1);

    this._hide();
    this._show();
  }

  destroy() {
    this._removeTw();

    super.destroy();
  }

  _show() {
    this._removeTw();
    this._positions = this._getHintPositions();
    this._currentPoint = 0;

    this._firstTimeShow();
  }

  _firstTimeShow() {
    const point = this._positions[this._currentPoint];
    this.scale.set(1);
    this.alpha = 1;
    this.position.set(point.x, point.y);
    this.angle = 180;
    this.visible = true;
    const { x, y, angle } = this;

    this.game.add
      .tween(this)
      .from({ x: x + 50, y: y + 50, angle: angle + 30 }, 400, Phaser.Easing.Cubic.Out, true, 0)
      .onComplete.addOnce(() => {
        this._pointHand();
      });
    this.game.add.tween(this).from({ alpha: 0 }, 400, Phaser.Easing.Cubic.Out, true, 0);
  }

  _pointHand() {
    handScaleTw(this).onComplete.addOnce(() => {
      this._currentPoint += 1;
      if (this._currentPoint === this._positions.length) {
        this._currentPoint = 0;
      }
      this._moveHand(this._positions[this._currentPoint]);
    });
  }

  _moveHand(pos) {
    handMoveTw(this, pos).onComplete.addOnce(() => {
      this._pointHand();
    });
  }

  _hide() {
    this._removeTw();
    this.visible = false;
  }

  _getHintPositions() {
    const cta = getViewByProperty('name', 'CTAEmbeddedPopup');
    const pos = cta.getHintPosition();
    return pos;
  }

  _removeTw() {
    this.game.tweens.removeFrom(this.scale);
    this.game.tweens.removeFrom(this);
  }
}
