// @ts-nocheck
/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getOptionButtonPatchConfig } from '../../configs/ninepatch-configs';
import { getOptionsConfig } from '../../configs/options-config';
import { AdStatus, OptionState, OptionType } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { difference } from '../../utils/array/difference';
import { Container } from '../../utils/container';
import { OptionComponent } from './option-component';

export class OptionsSet extends Container {
  constructor() {
    super();

    this._options = [];

    lego.event
      .on(ModelEvents.OptionsSetModel.OptionsUpdate, this._onOptionsUpdate, this)
      .on(ModelEvents.OptionModel.StateUpdate, this._onOptionStateUpdate, this)
      .on(ModelEvents.GameModel.CurrentMoneyAmountUpdate, this._onCurrentMoneyAmountUpdate, this)
      .on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
  }

  get name() {
    return 'OptionsSet';
  }

  getBounds() {
    const offset = 10;
    const { length } = getOptionsConfig();
    const { height } = getOptionButtonPatchConfig();

    return new Phaser.Rectangle(-100, -(length * (height + offset)) / 2, 200, length * (height + offset));
  }

  getOptionByUuid(uuid) {
    return this._options.find(option => option.uuid === uuid);
  }

  _onAdStatusUpdate(status) {
    switch (status) {
      case AdStatus.PreCta:
        this._disableOptions();
        break;
      default:
        break;
    }
  }

  _onOptionsUpdate(options) {
    const offset = 10;
    const { height } = getOptionButtonPatchConfig();

    const optionsModel = options.map(f => f.uuid);
    const optionsView = this._options.map(f => f.uuid);

    const newOptions = difference(optionsModel, optionsView);

    newOptions.forEach(uuid => {
      const option = new OptionComponent(uuid);

      option.onClick.add(this._onOptionClick, this);
      option.position.set(0, this._options.length * (height + offset) - (height + offset));

      this._options.push(option);

      this.addChild(option);
    });
  }

  _onOptionStateUpdate(newState, previousState, uuid) {
    const option = this.getOptionByUuid(uuid);

    switch (newState) {
      case OptionState.Enable:
        option.enable();
        break;
      case OptionState.Disable:
        option.disable();
        break;
      default:
        break;
    }
  }

  _onCurrentMoneyAmountUpdate(newAmount) {
    this._options.forEach(option => {
      const { uuid } = option;
      const { cost } = store.game.optionsSetModel.getOptionByUuid(uuid);
      option.progressUpdate(newAmount / cost);
    });
  }

  _onOptionClick(uuid) {
    const { type } = store.game.optionsSetModel.getOptionByUuid(uuid);

    switch (type) {
      case OptionType.LevelUp:
        lego.event.emit(ViewEvents.OptionsSet.LevelUpClick, uuid);
        break;
      case OptionType.NewFiretrucks:
        lego.event.emit(ViewEvents.OptionsSet.NewFiretrucksClick, uuid);
        break;
      case OptionType.Captain:
        lego.event.emit(ViewEvents.OptionsSet.CaptainClick, uuid);
        break;
      default:
        break;
    }

    lego.event.emit(ViewEvents.OptionsSet.Click, uuid);
  }

  _disableOptions() {
    this._options.forEach(option => {
      option.onClick.remove(this._onOptionClick, this);
    });
  }
}
