/* eslint-disable class-methods-use-this */
import { getOptionButtonFillPatchConfig, getOptionButtonPatchConfig } from '../../configs/ninepatch-configs';
import { getOptionCostTextConfig, getOptionLabelTextConfig } from '../../configs/text-configs';
import { store } from '../../models/store';
import { fitText, makeNinePatch, makeText, pulse } from '../../utils';
import { Container } from '../../utils/container';

export class OptionComponent extends Container {
  constructor(uuid) {
    super();

    this.onClick = new Phaser.Signal();
    this._uuid = uuid;

    this._build();
  }

  get name() {
    return 'OptionComponent';
  }

  get uuid() {
    return this._uuid;
  }

  getHintPos() {
    return this.toGlobal(new Phaser.Point(80, 0));
  }

  enable() {
    this._bg.inputEnabled = true;
    this._fill.tint = 0xffffff;
    this._bringToTopComponents();
    this._pulseTw = pulse(this, 0.05);
  }

  disable() {
    this._bg.inputEnabled = false;
    this._fill.tint = 0xbdbdbd;
    this.bringToTop(this._hitArea);
    this.game.tweens.remove(this._pulseTw);
    this.scale.set(1);
  }

  progressUpdate(progress, force = false) {
    const { width, height } = this._fill;
    if (force) {
      this._updateGrWidth(this._maskRect, width * progress, height);
    } else {
      this.game.tweens.remove(this.updateTween);
      const obj = { value: this._maskRect.width };
      this.updateTween = this.game.add
        .tween(obj)
        .to({ value: width * progress }, 500, Phaser.Easing.Sinusoidal.Out, true)
        .onUpdateCallback(() => {
          this._updateGrWidth(this._maskRect, obj.value, height);
        });
    }
  }

  _updateGrWidth(gr, w, h) {
    const { x, y, width, height } = this._fill;
    gr.clear();
    gr.beginFill(0xff0000, 0.6);
    gr.drawRect(x - width / 2, y - height / 2, w, h);
    gr.endFill();
  }

  _bringToTopComponents() {
    this.bringToTop(this._bg);
    this.bringToTop(this._fill);
    this.bringToTop(this._maskRect);
    this.bringToTop(this._text);
    this.bringToTop(this._cost);
  }

  _onClick() {
    this.game.add
      .tween(this.scale)
      .to(
        {
          x: '-0.05',
          y: '-0.05'
        },
        25,
        Phaser.Easing.Back.Out,
        true,
        0,
        0,
        true
      )
      .onComplete.add(() => {
        this.onClick.dispatch(this._uuid);
      });
  }

  _build() {
    this._buildBg();
    this._buildFill();
    this._buildMask();
    this._buildLabel();
    this._buildCost();

    this._buildHitArea();
  }

  _buildBg() {
    const { name } = store.game.optionsSetModel.getOptionByUuid(this._uuid);
    this._bg = makeNinePatch(getOptionButtonPatchConfig());
    this._bg.name = name;
    this._bg.inputEnabled = true;
    this._bg.events.onInputDown.add(this._onClick, this);

    this.addChild(this._bg);
  }

  _buildFill() {
    this._fill = makeNinePatch(getOptionButtonFillPatchConfig());
    this.addChild(this._fill);
  }

  _buildMask() {
    const gr = this.game.add.graphics();
    gr.beginFill(0x00ff00);
    gr.drawRect(0, 0, 0, 0);
    this.addChild((this._maskRect = gr));

    this._fill.mask = gr;
  }

  _buildLabel() {
    const { width, height } = this._bg;
    const { name } = store.game.optionsSetModel.getOptionByUuid(this._uuid);
    this._text = makeText(getOptionLabelTextConfig(name));
    this._text.anchor.set(0.5);
    this._text.position.set(0, -15);
    fitText(this._text, width * 0.8, height * 0.8);

    this.addChild(this._text);
  }

  _buildCost() {
    const { width, height } = this._bg;
    const { cost } = store.game.optionsSetModel.getOptionByUuid(this._uuid);
    this._cost = makeText(getOptionCostTextConfig(cost.toString()));
    this._cost.anchor.set(0.5);
    this._cost.position.set(0, 15);
    fitText(this._cost, width * 0.8, height * 0.8);

    this.addChild(this._cost);
  }

  _buildHitArea() {
    const { width, height } = this._bg;
    const { name } = store.game.optionsSetModel.getOptionByUuid(this._uuid);

    const gr = this.game.add.graphics();
    gr.beginFill(0xff0000, 0);
    gr.drawRect(-width / 2, -height / 2, width, height);
    gr.endFill();

    gr.name = name;
    gr.inputEnabled = true;

    this.addChild((this._hitArea = gr));
  }
}
