import { lego } from '@armathai/lego';
import { FiretruckState } from '../../constants';
import { store } from '../../models/store';
import { addCaptainCommand } from '../board/add-captain-command';
import { setFiretruckStateCommand } from '../board/set-firetruck-state-command';
import { debitCommand } from '../game/debit-command';

export function onCaptainClickCommand(uuid) {
  const firetruck = store.game.boardModel.getFirstFiretruckWithoutCaptain();
  const { cost } = store.game.optionsSetModel.getOptionByUuid(uuid);

  lego.command
    //
    .payload(firetruck)
    .execute(addCaptainCommand)

    .payload(firetruck, FiretruckState.Moving)
    .execute(setFiretruckStateCommand)

    .payload(cost)
    .execute(debitCommand);
}
