/* eslint-disable class-methods-use-this */
import { getTutorialSequenceGridConfig } from '../../configs/grid-configs';
import { DudeTypes } from '../../constants';
import { Container } from '../../utils/container';
import { DudeView } from '../game/board/dude-view';

export class TutorialSequenceView extends Container {
  constructor(config) {
    super();

    this._showTween = null;
    this._hideTween = null;

    this._config = config;
    this.alpha = 0;
    this._force = true;

    this._build();
  }

  getGridConfig() {
    return getTutorialSequenceGridConfig();
  }

  destroy() {
    this.game.tweens.remove(this._showTween);
    this.game.tweens.remove(this._hideTween);

    super.destroy();
  }

  rebuild() {
    this._dude && this._dude.rebuild(this._force);
  }

  show() {
    this.alpha = 1;
    const tw = this._dude.show();

    return tw;
  }

  hide() {
    this._force = false;
    const tw = this._dude.hide();
    tw.onComplete.add(() => {
      this.alpha = 0;
    });
    return tw;
  }

  _build() {
    this._dude = new DudeView(DudeTypes.Tutorial);
    this.addChild(this._dude);
  }
}
