import { ObservableModel } from '../observable-model';
import { BoardModel } from './board/board-model';
import { OptionsSetModel } from './options-set-model';
import { TimerModel } from './timer-model';

export class GameModel extends ObservableModel {
  constructor() {
    super('GameModel');

    this._timer = null;
    this._boardModel = null;
    this._optionsSetModel = null;
    this._goalMoneyAmount = null;
    this._currentMoneyAmount = null;

    this.makeObservable();
  }

  get timer() {
    return this._timer;
  }

  get boardModel() {
    return this._boardModel;
  }

  get optionsSetModel() {
    return this._optionsSetModel;
  }

  get goalMoneyAmount() {
    return this._goalMoneyAmount;
  }

  get currentMoneyAmount() {
    return this._currentMoneyAmount;
  }

  set timer(value) {
    this._timer = value;
  }

  set goalMoneyAmount(value) {
    this._goalMoneyAmount = value;
  }

  set currentMoneyAmount(value) {
    this._currentMoneyAmount = value;
  }

  initialize() {
    this._initializeOptionsSetModel();
    this._initializeBoardModel();
    this._initializeMoney();
  }

  initializeTimer() {
    this._timer = new TimerModel();
  }

  destroyTimer() {
    this._timer.destroy();
    this._timer = null;
  }

  _initializeOptionsSetModel() {
    this._optionsSetModel = new OptionsSetModel();
    this._optionsSetModel.initialize();
  }

  _destroyOptionsSetModel() {
    this._optionsSetModel.destroy();
    this._optionsSetModel = null;
  }

  _initializeBoardModel() {
    this._boardModel = new BoardModel();
    this._boardModel.initialize();
  }

  _destroyBoardModel() {
    this._boardModel.destroy();
    this._boardModel = null;
  }

  destroy() {
    this._destroyBoardModel();
    this._destroyOptionsSetModel();
  }

  _initializeMoney() {
    this._goalMoneyAmount = CI_API.Globals.PARAMS.cash_to_cta;
    this._currentMoneyAmount = 0;
  }
}
