import { DudeTypes } from '../constants';
import { isSquareLikeScreen } from '../utils';
import { getBubbleArrowImageConfig, getDudeImageConfig } from './image-configs';
import { getTutorialBubblePatchConfig } from './ninepatch-configs';
import { getCtaBubbleTextConfig, getTutorialTextConfig } from './text-configs';

function getTutorialDudeBubbleConfig() {
  const width = LP(500, 500);
  const height = LP(200, 200);
  const posY = LP(isSquareLikeScreen() ? -30 : -10, -75);

  return {
    ninePatchConfig: getTutorialBubblePatchConfig(width, height),
    arrowImageConfig: getBubbleArrowImageConfig(),
    textConfig: getTutorialTextConfig('Tutorial Text'),
    angle: 0,
    pos: { x: LP(200, 0), y: posY }
  };
}

function getTutorialDudeImageConfig() {
  return {
    imageConfig: getDudeImageConfig(),
    pos: { x: LP(375, 175), y: LP(70, 100) },
    scale: { x: LP(0.8, 0.8), y: LP(0.8, 0.8) }
  };
}

function getWinCtaDudeBubbleConfig() {
  const width = LP(475, 475);
  const height = LP(300, 300);
  const posY = LP(isSquareLikeScreen() ? 100 : 100, -20);

  return {
    ninePatchConfig: getTutorialBubblePatchConfig(width, height),
    arrowImageConfig: getBubbleArrowImageConfig(),
    textConfig: getCtaBubbleTextConfig('Click now to become the next fire chief!', width),
    angle: 0,
    textPos: { x: 0, y: LP(-265, -265) },
    playBtnPos: { x: 0, y: LP(-135, -135) },
    pos: { x: LP(175, isSquareLikeScreen() ? 100 : 0), y: posY }
  };
}

function getWinCtaDudeImageConfig() {
  return {
    imageConfig: getDudeImageConfig(),
    pos: { x: LP(375, 175), y: LP(50, 145) },
    scale: { x: LP(0.8, 0.8), y: LP(0.8, 0.8) }
  };
}

function getLoseCtaDudeBubbleConfig() {
  const width = LP(450, 450);
  const height = LP(300, 300);
  const posY = LP(isSquareLikeScreen() ? 100 : 100, -20);

  return {
    ninePatchConfig: getTutorialBubblePatchConfig(width, height),
    arrowImageConfig: getBubbleArrowImageConfig(),
    textConfig: getCtaBubbleTextConfig("Don't give up now,\nwe need you!", width),
    angle: 0,
    textPos: { x: 0, y: LP(-265, -265) },
    playBtnPos: { x: 0, y: LP(-135, -135) },
    pos: { x: LP(175, isSquareLikeScreen() ? 100 : 0), y: posY }
  };
}

function getLoseCtaDudeImageConfig() {
  return {
    imageConfig: getDudeImageConfig(),
    pos: { x: LP(375, 175), y: LP(50, 145) },
    scale: { x: LP(0.8, 0.8), y: LP(0.8, 0.8) }
  };
}

export function getDudeConfig(type) {
  const obj = {};
  switch (type) {
    case DudeTypes.Tutorial:
      obj.bubbleConfig = getTutorialDudeBubbleConfig();
      obj.imageConfig = getTutorialDudeImageConfig();
      break;
    case DudeTypes.WinCta:
      obj.bubbleConfig = getWinCtaDudeBubbleConfig();
      obj.imageConfig = getWinCtaDudeImageConfig();
      break;
    case DudeTypes.LoseCta:
      obj.bubbleConfig = getLoseCtaDudeBubbleConfig();
      obj.imageConfig = getLoseCtaDudeImageConfig();
      break;

    default:
      break;
  }

  return obj;
}
