import { CellAlign, CellScale } from '@armathai/phaser2-grid';
import { isNarrowScreen } from '../../utils';

export function getCTABottomLayerGridLandscapeConfig() {
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta_bottom_layer',
    cells: [
      {
        name: 'content',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      },
      {
        name: 'mask',
        align: CellAlign.RightBottom,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      },
      {
        name: 'play_btn',
        bounds: { x: 0.5475, y: 0.71, width: 0.4, height: 0.2 }
      },
      {
        name: 'logo',
        scale: CellScale.ShowAll,
        bounds: { x: 0.5475, y: 0.34, width: 0.4, height: 0.29 }
      }
    ]
  };
}

export function getCTABottomLayerGridPortraitConfig() {
  const lY = isNarrowScreen() ? 0.66 : 0.6275;
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta_bottom_layer',
    cells: [
      {
        name: 'content',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      },
      {
        name: 'mask',
        align: CellAlign.RightBottom,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      },
      {
        name: 'play_btn',
        bounds: { x: 0, y: 0.83, width: 1, height: 0.125 }
      },
      {
        name: 'logo',
        scale: CellScale.ShowAll,
        bounds: { x: 0, y: lY, width: 1, height: 0.1625 }
      }
    ]
  };
}
