import { OptionState } from '../../constants';
import { ObservableModel } from '../observable-model';

export class OptionModel extends ObservableModel {
  constructor(name, cost, type) {
    super('OptionModel');

    this._name = name;
    this._cost = cost;
    this._type = type;
    this._state = OptionState.Unknown;

    this.makeObservable();
  }

  destroy() {
    this._state = OptionState.Disable;
    super.destroy();
  }

  get name() {
    return this._name;
  }

  get cost() {
    return this._cost;
  }

  get type() {
    return this._type;
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  enable() {
    this._state = OptionState.Enable;
  }

  disable() {
    this._state = OptionState.Disable;
  }
}
