import { lego } from '@armathai/lego';
import { FiretruckState, OptionState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { ViewEvents } from '../../events/view-events';
import { store } from '../../models/store';
import { getViewByProperty, handMoveTw, handPumpTw, searchAtlasByFrame } from '../../utils';

export class HintView extends Phaser.Sprite {
  constructor() {
    const texture = searchAtlasByFrame('ui/hint.png');
    super(CI_API.game, 0, 0, texture.key, texture.frame);
    this.anchor.set(0.5, 1);

    lego.event.on(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);
    this._hintOnTrucks = null;
    this._hide();
  }

  destroy() {
    this._removeTw();
    lego.event.off(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);

    super.destroy();
  }

  _onHintVisibleUpdate(visible) {
    visible ? this._show() : this._hide();
  }

  _show() {
    this._removeTw();
    this._positions = this._getHintPositions();
    if (this._positions.length === 0) {
      lego.event.emit(ViewEvents.HintView.NoHintFound);
      return;
    }
    this._currentPoint = 0;

    this._firstTimeShow();
  }

  _firstTimeShow() {
    const point = this._positions[this._currentPoint];
    this.scale.set(1);
    this.alpha = 1;
    this.position.set(point.x, point.y);
    this.visible = true;
    const { x, y, angle } = this;

    this.game.add
      .tween(this)
      .from({ x: x + 50, y: y + 50, angle: angle + 30 }, 400, Phaser.Easing.Cubic.Out, true, 0)
      .onComplete.addOnce(() => {
        this._pointHand();
      });
    this.game.add.tween(this).from({ alpha: 0 }, 400, Phaser.Easing.Cubic.Out, true, 0);
  }

  _pointHand() {
    if (this._hintOnTrucks) {
      handPumpTw(this).onComplete.addOnce(() => {
        this._currentPoint += 1;
        if (this._currentPoint === this._positions.length) {
          this._currentPoint = 0;
        }
        this._moveHand(this._positions[this._currentPoint]);
      });
    } else {
      handPumpTw(this, 0, 30).onComplete.addOnce(() => {
        this._currentPoint += 1;
        if (this._currentPoint === this._positions.length) {
          this._currentPoint = 0;
        }
        this._moveHand(this._positions[this._currentPoint]);
      });
    }
  }

  _moveHand(pos) {
    handMoveTw(this, pos).onComplete.addOnce(() => {
      this._pointHand();
    });
  }

  _hide() {
    this._removeTw();
    this.visible = false;
  }

  _getHintPositions() {
    const { firetrucks } = store.game.boardModel;
    const { options } = store.game.optionsSetModel;
    const truckModels = firetrucks.filter(truck => truck.state === FiretruckState.Idle && !truck.captain);
    const optionModels = options.filter(option => option.state === OptionState.Enable);
    let pos = [];

    if (truckModels.length !== 0 && optionModels.length !== 0) {
      pos = Math.random() > 0.5 ? this._getTruckHintPosition(truckModels) : this._getButtonHintPosition(optionModels);
    } else if (truckModels.length === 0 && optionModels.length !== 0) {
      pos = this._getButtonHintPosition(optionModels);
    } else if (truckModels.length !== 0 && optionModels.length === 0) {
      pos = this._getTruckHintPosition(truckModels);
    }

    // const pos = truckModels ? this._getTruckHintPosition(truckModels) : this._getButtonHintPosition(optionModels);
    return [...pos];
  }

  _getTruckHintPosition(truckModels) {
    this._hintOnTrucks = true;
    this.angle = 0;
    const boardView = getViewByProperty('name', 'BoardView');

    const pos = [];
    truckModels.forEach(t => {
      const truckView = boardView.getFiretruckByUuid(t.uuid);
      pos.push(this.parent.toLocal(truckView.getHintPos(), null));
    });

    return pos;
  }

  _getButtonHintPosition(optionModels) {
    this._hintOnTrucks = false;

    const pos = [];
    if (optionModels.length !== 0) {
      const optionSetView = getViewByProperty('name', 'OptionsSet');
      optionModels.forEach(o => {
        const optionComponent = optionSetView.getOptionByUuid(o.uuid);
        pos.push(this.parent.toLocal(optionComponent.getHintPos(), null));
      });
    }
    this.angle = 90;

    return pos;
  }

  _removeTw() {
    this.game.tweens.removeFrom(this.scale);
    this.game.tweens.removeFrom(this);
  }
}
