/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getCtaPeelPlayButtonConfig } from '../../configs/button-configs';
import { getCTABottomLayerGridConfig } from '../../configs/grid-configs';
import { getCTALogoImageConfig } from '../../configs/image-configs';
import { ViewEvents } from '../../events/view-events';
import { isNarrowScreen, makeImage, makePixel, pulse } from '../../utils';
import { Button } from '../../utils/button/button';

export class CtaBottomLayer extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    super.build(this.getGridConfig());

    this._build();
    this._show();
  }

  _getPeelPoints() {
    const vP = isNarrowScreen() ? -830 : -850;
    const hP = isNarrowScreen() ? -990 : -850;
    const vPoint = LP([0, -920], [0, vP]);
    const hPoint = LP([-800, 0], [hP, 0]);

    return {
      hPoint,
      vPoint
    };
  }

  _getGlidePoints() {
    const vP = isNarrowScreen() ? -850 : -862.5;
    const hP = isNarrowScreen() ? -1015 : -875;

    const vPoint = LP([0, -945], [0, vP]);
    const hPoint = LP([-825, 0], [hP, 0]);

    return {
      hPoint,
      vPoint
    };
  }

  getGridConfig() {
    return getCTABottomLayerGridConfig();
  }

  _show() {
    this._showPagePeel();
  }

  _build() {
    this._buildSolidBg();
    this._buildLogo();
    this._buildPlayButton(getCtaPeelPlayButtonConfig());
    this._buildMask();
  }

  _buildSolidBg() {
    this._bg = makePixel({ alpha: 1, tint: 0x46757d });
    this.setChild('content', this._bg);
  }

  _buildLogo() {
    const logo = makeImage(getCTALogoImageConfig());
    this.setChild('logo', (this._logo = logo));
  }

  _buildPlayButton(config) {
    const btn = new Button(config);
    btn.onClick.add(this._onPlayClick, this);
    pulse(btn);
    this.setChild('play_btn', (this._playBtn = btn));
  }

  _onPlayClick() {
    lego.event.emit(ViewEvents.CtaView.PlayClick);
  }

  _buildMask() {
    this._maskShape = this.game.add.graphics(0, 0);

    this._maskShape.beginFill(0x00ff00, 1);
    this._maskShape.drawPolygon(0, -25, 0, 0, -25, 0);

    this.mask = this._maskShape;
    this.setChild('mask', this._maskShape);
  }

  _showPagePeel() {
    this.visible = true;
    const { vPoint, hPoint } = this._getPeelPoints();

    const cPoint = [0, 0];
    this.game.add
      .tween(this._maskShape.currentPath.shape.points)
      .to([...vPoint, ...cPoint, ...hPoint], 750, Phaser.Easing.Sinusoidal.InOut, true, 0)
      .onComplete.add(this._glide, this);
  }

  _glide() {
    const cPoint = [0, 0];
    const { vPoint, hPoint } = this._getGlidePoints();

    this.game &&
      this.game.add
        .tween(this._maskShape.currentPath.shape.points)
        .to([...vPoint, ...cPoint, ...hPoint], 1000, Phaser.Easing.Sinusoidal.InOut, true, 0, -1, true);
  }
}
