export function getBackgroundImageConfig() {
  return {
    frame: 'bg/game.jpg'
  };
}

export function getBubbleArrowImageConfig() {
  return {
    frame: 'tutorial/bubble_arrow.png',
    anchor: {
      x: 1,
      y: 1
    }
  };
}

export function getDudeImageConfig() {
  return {
    frame: 'tutorial/tutorial_char.png'
  };
}

export function getBgPartImageConfig(part) {
  return {
    frame: `bg/bg_part_${part}.png`
  };
}

export function getCashIconImageConfig() {
  return {
    frame: 'ui/cash_icon.png'
  };
}

export function getForwardFiretruckImageConfig(level) {
  return {
    frame: `firetruck/truck_forward_lvl_${level}.png`
  };
}

export function getBackwardFiretruckImageConfig(level) {
  return {
    frame: `firetruck/truck_backward_lvl_${level}.png`
  };
}

export function getFiretruckCharacterImageConfig() {
  return {
    frame: 'firetruck/firetruck_char.png'
  };
}

export function getCaptainImageConfig(type) {
  return {
    frame: `firetruck/captain_${type}.png`
  };
}

export function getCTAIconImageConfig(side) {
  return {
    frame: `cta/cta_icon_${side}.png`
  };
}

export function getCTALogoImageConfig() {
  return {
    frame: 'ui/logo.png'
  };
}
