import { CellAlign } from '@armathai/phaser2-grid';

export function getUIGridLandscapeConfig() {
  const { timer } = CI_API.Globals.PARAMS;
  const moneyBarWidth = timer ? 0.4 : 0.4;
  const moneyBarAlign = timer ? CellAlign.RightCenter : CellAlign.Center;
  return {
    // debug: { color: 0x4287f5 },
    name: 'ui',
    cells: [
      {
        name: 'p_cta',
        offset: { x: 0, y: -20 },
        align: CellAlign.CenterBottom,
        bounds: { x: 0.575, y: 0.8, width: 0.425, height: 0.2 }
      },
      {
        name: 'money_bar',
        align: moneyBarAlign,
        bounds: { x: 0.6, y: 0.025, width: moneyBarWidth, height: 0.2 }
      },
      {
        name: 'options_set',
        offset: { x: 0, y: -20 },
        align: CellAlign.CenterBottom,
        bounds: { x: 0.025, y: 0.45, width: 0.25, height: 0.55 }
      },
      {
        name: 'timer',
        align: CellAlign.LeftCenter,
        padding: { x: 0.1, width: 0.9 },
        bounds: { x: 0.8, y: 0.025, width: 0.2, height: 0.2 }
      }
    ]
  };
}

