import { lego } from '@armathai/lego';
import { FiretruckState, GameOverReasons, MoneyMultiplier } from '../../constants';
import { firetruckHasCaptainGuard } from '../../guards/board/firetruck-has-captain-guard';
import { goalMoneyReachedGuard } from '../../guards/game/goal-money-reached-guard';
import { store } from '../../models/store';
import { showCtaCommand } from '../ad/cta/show-cta-command';
import { creditCommand } from '../game/credit-command';
import { setFiretruckStateCommand } from './set-firetruck-state-command';

export function onFiretruckMoveCompleteCommand(uuid) {
  const firetruck = store.game.boardModel.getFiretruckByUuid(uuid);
  const { level } = firetruck;

  lego.command
    //
    .payload(firetruck, FiretruckState.Stopped)
    .execute(setFiretruckStateCommand)

    .payload(firetruck, FiretruckState.Idle)
    .execute(setFiretruckStateCommand)

    .payload(level * MoneyMultiplier)
    .execute(creditCommand)

    .guard(goalMoneyReachedGuard)
    .payload(GameOverReasons.ItemsToCtaReached)
    .execute(showCtaCommand)

    .payload(firetruck, FiretruckState.Moving)
    .guard(firetruckHasCaptainGuard)
    .execute(setFiretruckStateCommand);
}
