/* eslint-disable no-nested-ternary */
import { CellAlign, CellScale } from '@armathai/phaser2-grid';
import { isNarrowScreen, isSquareLikeScreen } from '../../utils';

export function getCTAGridLandscapeConfig() {
  const x = isSquareLikeScreen() ? 0.1 : 0;
  const y = isSquareLikeScreen() ? 0.225 : isNarrowScreen() ? 0.125 : 0.2;
  const width = 1 - x * 2;
  const height = 1 - y * (isSquareLikeScreen() ? 1.5 : 1.75);

  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta',
    cells: [
      {
        name: 'dude',
        align: CellAlign.CenterBottom,
        bounds: { x: 0, y: 0.1, width: 1, height: 0.9 }
      },
      {
        name: 'popup',
        bounds: { x, y, width, height }
      },
      {
        name: 'play_button',
        bounds: { x: 0.25, y: 0.75, width: 0.5, height: 0.25 }
      },
      {
        name: 'blocker',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      }
    ]
  };
}

export function getCTAGridPortraitConfig() {
  const y = LP(0.25, isNarrowScreen() ? 0.125 : isSquareLikeScreen() ? 0.125 : 0.175);
  const px = LP(0, isSquareLikeScreen() ? 0.15 : 0);
  return {
    // debug: { color: 0x4bdbd4 },
    name: 'cta',
    cells: [
      {
        name: 'dude',
        align: CellAlign.CenterBottom,
        bounds: { x: 0, y: 0.1, width: 1, height: 0.9 }
      },
      {
        name: 'popup',
        padding: { x: px, width: 1 - px * 2 },
        bounds: { x: 0, y, width: 1, height: 0.75 }
      },
      {
        name: 'play_button',
        bounds: { x: 0, y: 0.75, width: 1, height: 0.25 }
      },
      {
        name: 'blocker',
        scale: CellScale.Fill,
        bounds: { x: 0, y: 0, width: 1, height: 1 }
      }
    ]
  };
}
