/* eslint-disable class-methods-use-this */
import { getCTAGridConfig } from '../../configs/grid-configs';
import { DudeTypes } from '../../constants';
import { postRunnable } from '../../utils';
import { DudeView } from '../game/board/dude-view';
import { CTAView } from './cta-view';

export class CTAWinView extends CTAView {
  constructor() {
    super();

    this.alpha = 0;
    this._build();
  }

  getGridConfig() {
    return getCTAGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }
    this._dude && this._dude.rebuild(true);

    super.rebuild(config);
  }

  _build() {
    this.build(this.getGridConfig());
    this._buildDude();
  }

  _buildDude() {
    const dude = new DudeView(DudeTypes.WinCta);
    dude.onPlayClick.add(() => this._onPlayClick());
    this.setChild('dude', (this._dude = dude));
    postRunnable(() =>
      this._dude.show('cta').onStart.add(() => {
        this.game.add.tween(this).to({ alpha: 1 }, 250, Phaser.Easing.Sinusoidal.InOut, true, 0);
      })
    );
  }
}
