/* eslint-disable class-methods-use-this */
import { getCtaPlayButtonConfig } from '../../../configs/button-configs';
import { getDudeConfig } from '../../../configs/dude-config';
import { DudeTypes } from '../../../constants';
import { hideToRight, makeImage, showFromRight } from '../../../utils';
import { BubbleContainer } from '../../../utils/bubble-container';
import { Button } from '../../../utils/button/button';
import { Container } from '../../../utils/container';

export class DudeView extends Container {
  constructor(type) {
    super();

    this._type = type;
    this._config = getDudeConfig(this._type);

    this.onPlayClick = new Phaser.Signal();

    this._build();
  }

  getBounds() {
    const { left: dL, right: dR, bottom: dB } = this._dude;
    const { left: bL, right: bR, top: bT } = this._bubble;

    const l = dL < bL ? dL : bL;
    const r = dR > bR ? dR : bR;

    const width = r - l;
    const height = Math.abs(bT - dB);

    return new Phaser.Rectangle(-width / 2, -height / 2 - 20, width, height);
  }

  rebuild(force = false) {
    this.game.tweens.remove(this._bubbleShowTw);
    this.game.tweens.remove(this._dudeShowTw);
    this.game.tweens.remove(this._bubbleHideTw);
    this.game.tweens.remove(this._dudeHideTw);

    const { imageConfig, bubbleConfig } = getDudeConfig(this._type);

    if (this._bubble) {
      this._bubble.destroy();
      force && this._buildBubble(bubbleConfig);
      force && (this._bubble.alpha = 1);
    }

    if (this._dude) {
      this._dude.destroy();
      force && this._buildDude(imageConfig);
    }
  }

  show(from) {
    this._bubble.alpha = 0;
    this._bubbleShowTw = this.game.add
      .tween(this._bubble.scale)
      .from({ x: 0, y: 0 }, 500, Phaser.Easing.Sinusoidal.InOut, false, 0, 0, false);
    this._dudeShowTw = showFromRight(this._dude, 200, 500, false);

    this._dudeShowTw.onComplete.add(() => {
      this._bubbleShowTw.start();
      this._bubble.alpha = 1;
    });

    const tw = from === 'cta' ? this._dudeShowTw : this._bubbleShowTw;

    return tw;
  }

  hide() {
    this._bubbleHideTw = this.game.add
      .tween(this._bubble.scale)
      .to({ x: 0, y: 0 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 0, 0, false);

    this._dudeHideTw = hideToRight(this._dude, 0, 500, false, false);

    this._bubbleHideTw.onComplete.add(() => {
      this._bubble.alpha = 0;
      this._bubble.scale.set(1);
      this._dudeHideTw.start();
    });

    return this._dudeHideTw;
  }

  _build() {
    const { imageConfig, bubbleConfig } = this._config;
    this._buildBubble(bubbleConfig);
    this._buildDude(imageConfig);

    // this._drawBounds();
  }

  _buildBubble(bubbleConfig) {
    const { pos } = bubbleConfig;
    const bubble = new BubbleContainer(bubbleConfig);
    bubble.alpha = 0;

    if (this._type === DudeTypes.WinCta) {
      this._buildPlayButton(getCtaPlayButtonConfig(), bubbleConfig.playBtnPos, bubble);
    }

    if (this._type === DudeTypes.LoseCta) {
      this._buildPlayButton(getCtaPlayButtonConfig(), bubbleConfig.playBtnPos, bubble);
    }
    bubble.position.set(pos.x, pos.y);

    this.addChild((this._bubble = bubble));
  }

  _buildDude(config) {
    const { imageConfig, scale, pos } = config;
    const dude = makeImage(imageConfig);
    dude.position.set(pos.x, pos.y);
    dude.scale.set(scale.x, scale.y);

    this.addChild((this._dude = dude));
  }

  _drawBounds() {
    const { x, y, width, height } = this.getBounds();

    const gr = this.game.add.graphics();
    gr.beginFill(0x0000ff, 0.5);
    gr.drawRect(x, y, width, height);
    gr.endFill();

    this.addChild(gr);
  }

  _buildPlayButton(config, pos, bubble) {
    const btn = new Button(config);
    btn.onClick.add(this._onPlayClick, this);
    btn.position.set(bubble.centerX, pos.y);
    bubble.addChild((this._playBtn = btn));
  }

  _onPlayClick() {
    this.onPlayClick.dispatch();
  }
}
