import { FiretruckMaxLevel, FiretruckState } from '../../../constants';
import { ObservableModel } from '../../observable-model';

export class FiretruckModel extends ObservableModel {
  constructor() {
    super('FiretruckModel');

    this._level = 1;
    this._captain = false;
    this._state = FiretruckState.Unknown;

    this.makeObservable();
  }

  get level() {
    return this._level;
  }

  get captain() {
    return this._captain;
  }

  get state() {
    return this._state;
  }

  set level(value) {
    this._level = value;
  }

  set captain(value) {
    this._captain = value;
  }

  set state(value) {
    this._state = value;
  }

  levelUp() {
    this._level = Math.min(this._level + 1, FiretruckMaxLevel);
  }

  addCaptain() {
    this._captain = true;
  }

  initialize() {
    this._state = FiretruckState.Idle;
  }
}
