import { ObservableModel } from '../../observable-model';
import { FiretruckModel } from './firetruck-model';

export class BoardModel extends ObservableModel {
  constructor() {
    super('BoardModel');

    this._firetrucks = [];

    this.makeObservable();
  }

  get firetrucks() {
    return this._firetrucks;
  }

  getFiretruckByUuid(uuid) {
    return this._firetrucks.find(truck => truck.uuid === uuid);
  }

  getFirstFiretruckByLevel(level) {
    return this._firetrucks.find(truck => truck.level === level);
  }

  getFirstFiretruckWithoutCaptain() {
    return this._firetrucks.find(truck => !truck.captain);
  }

  addFiretruck() {
    const firetruck = new FiretruckModel();
    this._firetrucks = [...this._firetrucks, firetruck];
    firetruck.initialize();
  }

  initialize() {
    this._initializeFiretrucks();
  }

  _initializeFiretrucks() {
    const initialAmount = 1;

    for (let i = 0; i < initialAmount; i += 1) {
      this.addFiretruck();
    }
  }
}
