/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getCashIconImageConfig } from '../../configs/image-configs';
import { getMoneyBarBgPatchConfig } from '../../configs/ninepatch-configs';
import { getMoneyBarTextConfig } from '../../configs/text-configs';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { fitText, makeImage, makeNinePatch, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class MoneyBar extends Container {
  constructor() {
    super();

    this._build();

    lego.event
      .on(ModelEvents.GameModel.GoalMoneyAmountUpdate, this._onGoalMoneyAmountUpdate, this)
      .on(ModelEvents.GameModel.CurrentMoneyAmountUpdate, this._onCurrentMoneyAmountUpdate, this);
  }

  get name() {
    return 'MoneyBar';
  }

  getCashPos() {
    return this.toGlobal(new Phaser.Point(this._cashIcon.x, this._cashIcon.y));
  }

  _onGoalMoneyAmountUpdate(newAmount) {
    const money = `${store.game.currentMoneyAmount || 0}/${newAmount}`;
    this._text && this._text.destroy();
    this._buildText(money);
  }

  _onCurrentMoneyAmountUpdate(newAmount) {
    const money = `${newAmount}/${store.game.goalMoneyAmount || 0}`;
    this._text && this._text.destroy();
    this._buildText(money);
  }

  _build() {
    this._buildBg();
    this._buildText();
    this._buildCashIcon();
  }

  _buildBg() {
    this._bg = makeNinePatch(getMoneyBarBgPatchConfig());
    this.addChild(this._bg);
  }

  _buildText(money = `${0}/${0}`) {
    const { width, height } = this._bg;
    this._text = makeText(getMoneyBarTextConfig(money));
    this._text.anchor.set(1, 0.5);
    this._text.position.set(this._bg.right - 20, 0);
    fitText(this._text, width * 0.6, height * 0.8);

    this.addChild(this._text);
  }

  _buildCashIcon() {
    this._cashIcon = makeImage(getCashIconImageConfig());
    this._cashIcon.position.set(this._bg.left + this._cashIcon.width - 20, 0);
    this.addChild(this._cashIcon);
  }
}
