import { getOptionsConfig } from '../../configs/options-config';
import { ObservableModel } from '../observable-model';
import { OptionModel } from './option-model';

export class OptionsSetModel extends ObservableModel {
  constructor() {
    super('OptionsSetModel');

    this._options = [];

    this.makeObservable();
  }

  get options() {
    return this._options;
  }

  destroy() {
    this._options.forEach(option => {
      option.destroy();
    });
    super.destroy();
  }

  getOptionByUuid(uuid) {
    return this._options.find(option => option.uuid === uuid);
  }

  addOption(name, cost, type) {
    const option = new OptionModel(name, cost, type);
    this._options = [...this._options, option];
  }

  initialize() {
    this._initializeOptions();
  }

  _initializeOptions() {
    const optionsConfig = getOptionsConfig();

    optionsConfig.forEach(config => {
      const { name, cost, type } = config;
      this.addOption(name, cost, type);
    });
  }
}
