import { MaxFiretrucks, OptionType } from '../../constants';
import { store } from '../../models/store';

export function updateOptionsAvailabilityCommand() {
  const { optionsSetModel, currentMoneyAmount, boardModel } = store.game;
  const { length: firetrucksAmount } = boardModel.firetrucks;

  optionsSetModel.options.forEach(option => {
    const { cost } = optionsSetModel.getOptionByUuid(option.uuid);
    const { type } = optionsSetModel.getOptionByUuid(option.uuid);

    switch (type) {
      case OptionType.LevelUp:
        currentMoneyAmount >= cost && boardModel.getFirstFiretruckByLevel(1) ? option.enable() : option.disable();
        break;
      case OptionType.NewFiretrucks:
        currentMoneyAmount >= cost && firetrucksAmount < MaxFiretrucks ? option.enable() : option.disable();
        break;
      case OptionType.Captain:
        currentMoneyAmount >= cost && boardModel.getFirstFiretruckWithoutCaptain() ? option.enable() : option.disable();
        break;
      default:
        break;
    }
  });
}
