/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getUIGridConfig } from '../../configs/grid-configs';
import { getCashIconImageConfig } from '../../configs/image-configs';
import { FiretruckState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import {
  getViewByProperty,
  hideToBottom,
  hideToRight,
  makeImage,
  postRunnable,
  showFromBottom,
  showFromRight
} from '../../utils';
import { randomInt } from '../../utils/number/random-int';
import { MoneyBar } from './money-bar';
import { OptionsSet } from './options-set';
import { PersistentCTAView } from './persistent-cta-view';
import { TimerView } from './timer-view';

export class UIView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    this._moneyBar = null;
    this._optionsSet = null;
    this._persistentCtaView = null;

    this._build();

    lego.event
      .on(ModelEvents.AdModel.PersistentCtaUpdate, this._onPersistentCtaUpdate, this)
      .on(ModelEvents.GameModel.OptionsSetModelUpdate, this._onOptionsSetUpdate, this)
      .on(ModelEvents.GameModel.TimerUpdate, this._onTimerUpdate, this)
      .on(ModelEvents.FiretruckModel.StateUpdate, this._onFiretruckStateUpdate, this);
  }

  get name() {
    return 'UIView';
  }

  getGridConfig() {
    return getUIGridConfig();
  }

  // BUILD
  _build() {
    this._buildMoneyBar();
  }

  _buildMoneyBar() {
    this._moneyBar = new MoneyBar();
    this.setChild('money_bar', this._moneyBar);
  }

  // TIMER
  _onTimerUpdate(timer) {
    if (timer) {
      this._buildTimer();
      this._showTimer();
    } else {
      this._hideTimer().onComplete.add(this._destroyTimer, this);
    }
  }

  _buildTimer() {
    this._timerView && this._timerView.destroy();
    this._timerView = new TimerView();

    this.setChild('timer', this._timerView);
  }

  _showTimer() {
    const twArg = [this._timerView, 0, 500, false, Phaser.Easing.Cubic.Out];
    const hideTw = showFromRight(...twArg);
    return hideTw;
  }

  _hideTimer() {
    const twArg = [this._timerView, 0, 500, false, Phaser.Easing.Cubic.In];
    const showTw = hideToRight(...twArg);
    return showTw;
  }

  _destroyTimer() {
    this._timerView.destroy();
  }

  // PERSISTENT
  _onPersistentCtaUpdate(persistentCta) {
    if (persistentCta) {
      this._buildPersistentCta();
      this._showPersistentCta();
    } else {
      this._hidePersistentCta().onComplete.add(this._destroyPersistentCta, this);
    }
  }

  _buildPersistentCta() {
    this.setChild('p_cta', (this._persistentCtaView = new PersistentCTAView()));
  }

  _showPersistentCta() {
    const twArg = [this._persistentCtaView, false, 0, 500, true, Phaser.Easing.Cubic.Out];
    this._persistentCtaView.alpha = 0;
    const showTw = showFromBottom(...twArg);

    postRunnable(() => showTw.start());
    showTw.onStart.add(() => {
      this._persistentCtaView.alpha = 1;
    });
    return showTw;
  }

  _hidePersistentCta() {
    const twArg = [this._persistentCtaView, 0, 500, false, Phaser.Easing.Cubic.In];
    const hideTw = hideToBottom(...twArg);
    return hideTw;
  }

  _destroyPersistentCta() {
    this._persistentCtaView.destroy();
  }

  // OPTIONS SET
  _onOptionsSetUpdate(optionsSet) {
    optionsSet && this._buildOptionsSet();
  }

  _buildOptionsSet() {
    this._optionsSet = new OptionsSet();
    this.setChild('options_set', this._optionsSet);
  }

  _destroyOptionsSet() {
    this._optionsSet.destroy();
  }

  // FIRETRUCK
  _onFiretruckStateUpdate(newState, previousState, uuid) {
    switch (newState) {
      case FiretruckState.Stopped:
        this._throwCash(uuid);
        break;
      default:
        break;
    }
  }

  _throwCash(uuid) {
    const count = 10;
    const boardView = getViewByProperty('name', 'BoardView');
    const globalPos = boardView.getFiretruckPosition(uuid);
    const localPos = this.toLocal(globalPos, undefined);
    const destinationGlobalPos = this._moneyBar.getCashPos();
    const destinationLocalPos = this.toLocal(destinationGlobalPos, undefined);

    for (let i = 0; i < count; i += 1) {
      const cash = makeImage(getCashIconImageConfig());
      const angle = randomInt(-180, 180);
      const xOffset = randomInt(-50, 50);
      const yOffset = randomInt(-50, 50);

      cash.angle = angle;
      cash.position.set(localPos.x + xOffset, localPos.y + yOffset);

      this.addChild(cash);

      this.game.add.tween(cash.scale).from({ x: 0, y: 0 }, 50, Phaser.Easing.Cubic.InOut, true, i * 50 - 50);

      const throwTw = this.game.add
        .tween(cash)
        .to(
          { x: destinationLocalPos.x, y: destinationLocalPos.y, angle: 0 },
          500,
          Phaser.Easing.Cubic.InOut,
          true,
          i * 50 - 50
        );

      throwTw.onComplete.add(() => {
        cash.destroy();
      });
    }
  }
}
