/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { BackgroundColumnsPosition } from '../../../configs/background-columns-position-config';
import {
  getBackwardFiretruckImageConfig,
  getBgPartImageConfig,
  getCaptainImageConfig,
  getFiretruckCharacterImageConfig,
  getForwardFiretruckImageConfig
} from '../../../configs/image-configs';
import { getAppearSparkleEmitterConfig } from '../../../configs/particles-configs';
import { ViewEvents } from '../../../events/view-events';
import { store } from '../../../models/store';
import { makeEmitter, makeImage, searchAtlasByFrame } from '../../../utils';
import { last } from '../../../utils/array/last';
import { Container } from '../../../utils/container';
import { FiretruckTimerComponent } from './firetruck-timer-component';

export class FiretruckComponent extends Container {
  constructor(uuid, position) {
    super();

    this.onClick = new Phaser.Signal();
    this._uuid = uuid;
    this._position = position;

    this._duration = 750;
    this._captain = null;
    this._characters = [];
    this._firetrucks = [];
    this._forwardFiretrucks = [];
    this._backwardFiretrucks = [];

    this._build();
  }

  get name() {
    return 'FiretruckComponent';
  }

  get uuid() {
    return this._uuid;
  }

  get firetrucks() {
    return this._firetrucks;
  }

  getHintPos() {
    return this.toGlobal(new Phaser.Point(30, -30));
  }

  enable() {
    this._firstForward.inputEnabled = true;
    this._secondForward.inputEnabled = true;
  }

  disable() {
    this._firstForward.inputEnabled = false;
    this._secondForward.inputEnabled = false;
  }

  levelUp(level) {
    const forward = searchAtlasByFrame(`firetruck/truck_forward_lvl_${level}.png`);
    const backward = searchAtlasByFrame(`firetruck/truck_backward_lvl_${level}.png`);

    this._firstForward.loadTexture(forward.key, forward.frame);
    this._secondForward.loadTexture(forward.key, forward.frame);
    this._firstBackward.loadTexture(forward.key, backward.frame);
    this._secondBackward.loadTexture(forward.key, backward.frame);
  }

  showCaptain() {
    this._captain.visible = true;

    this._sparkleEmitter = makeEmitter(getAppearSparkleEmitterConfig(this._captainLayer, 75, -30));
  }

  move() {
    let forwardTw;
    let backwardTw;
    this._forwardTw = [];
    this._backwardTw = [];

    this._firetrucks.forEach((firetruck, index) => {
      forwardTw = this.game.add
        .tween(firetruck)
        .to({ x: '-750', y: '400' }, this._duration, Phaser.Easing.Sinusoidal.InOut, true, index * 100, 0, false);
      this._forwardTw.push(forwardTw);
    });

    this._firetrucks.forEach((firetruck, index) => {
      backwardTw = this.game.add
        .tween(firetruck)
        .to({ x: '+750', y: '-400' }, this._duration, Phaser.Easing.Sinusoidal.InOut, false, index * 100, 0, false);
      this._backwardTw.push(backwardTw);
    });

    this._forwardTw.forEach(tw => {
      tw.universal = true;
    });
    this._backwardTw.forEach(tw => {
      tw.universal = true;
    });

    const firstForwardTw = this._forwardTw[0];
    const firstBackwardTw = this._backwardTw[0];
    const lastForwardTw = last(this._forwardTw);
    const lastBackwardTw = last(this._backwardTw);

    firstForwardTw.onStart.add(this._onForwardMovementStart, this);
    firstBackwardTw.onStart.add(this._onBackwardMovementStart, this);
    lastForwardTw.onComplete.add(this._onForwardMovementComplete, this);
    lastBackwardTw.onComplete.add(this._onBackwardMovementComplete, this);
  }

  _onForwardMovementStart() {
    this._characters.forEach(character => {
      character.visible = false;
    });

    !this._captain.visible && this._buildTimer();
  }

  _onBackwardMovementStart() {
    this._forwardFiretrucks.forEach(firetruck => {
      firetruck.visible = false;
    });
    this._backwardFiretrucks.forEach(firetruck => {
      firetruck.visible = true;
    });
  }

  _onForwardMovementComplete() {
    this._backwardTw.forEach(tw => {
      tw.start();
    });
  }

  _onBackwardMovementComplete() {
    this._characters.forEach(character => {
      !this._captain.visible && (character.visible = true);
    });
    this._forwardFiretrucks.forEach(firetruck => {
      firetruck.visible = true;
    });
    this._backwardFiretrucks.forEach(firetruck => {
      firetruck.visible = false;
    });
    lego.event.emit(ViewEvents.FiretruckComponent.MoveComplete, this._uuid);
  }

  _onClick() {
    this.onClick.dispatch(this._uuid);
  }

  _build() {
    this._buildCaptain();
    this._buildBgPart();
    this._buildFiretrucks();
    this._adjustLayers();
  }

  _buildTimer() {
    this._timer = new FiretruckTimerComponent(this._duration * 2);
    this._timer.scale.set(1.3);
    this._timer.position.set(0, -50);

    this.addChild(this._timer);
  }

  _buildCaptain() {
    this._captainLayer = new Container();
    this._captain = makeImage(getCaptainImageConfig(this._position + 1));
    this._captain.position.set(75, -30);
    this._captain.visible = false;
    this._captainLayer.add(this._captain);

    this.addChild(this._captainLayer);
  }

  _buildFiretrucks() {
    const { level } = store.game.boardModel.getFiretruckByUuid(this._uuid);

    this._firstFiretruck = new Container();
    this._secondFiretruck = new Container();

    const firstForward = makeImage(getForwardFiretruckImageConfig(level));
    const secondForward = makeImage(getForwardFiretruckImageConfig(level));
    const firstBackward = makeImage(getBackwardFiretruckImageConfig(level));
    const secondBackward = makeImage(getBackwardFiretruckImageConfig(level));

    const firstCharacter = makeImage(getFiretruckCharacterImageConfig());
    const secondCharacter = makeImage(getFiretruckCharacterImageConfig());

    secondForward.position.set(80, 40);
    secondBackward.position.set(80, 40);

    firstBackward.visible = false;
    secondBackward.visible = false;

    firstCharacter.position.set(10, 40);
    secondCharacter.position.set(90, 80);

    this._characters.push(...[firstCharacter, secondCharacter]);
    this._firetrucks.push(...[this._firstFiretruck, this._secondFiretruck]);
    this._forwardFiretrucks.push(...[firstForward, secondForward]);
    this._backwardFiretrucks.push(...[firstBackward, secondBackward]);

    this.addChild(this._firstFiretruck);
    this._firstFiretruck.addChild((this._firstBackward = firstBackward));
    this._firstFiretruck.addChild((this._firstForward = firstForward));
    this._firstFiretruck.addChild((this._firstCharacter = firstCharacter));

    this.addChild(this._secondFiretruck);
    this._secondFiretruck.addChild((this._secondBackward = secondBackward));
    this._secondFiretruck.addChild((this._secondForward = secondForward));
    this._secondFiretruck.addChild((this._secondCharacter = secondCharacter));

    firstForward.name = 'firetruck';
    firstForward.inputEnabled = true;
    firstForward.events.onInputDown.add(this._onClick, this);

    secondForward.name = 'firetruck';
    secondForward.inputEnabled = true;
    secondForward.events.onInputDown.add(this._onClick, this);
  }

  _buildBgPart() {
    const { first, second, third } = BackgroundColumnsPosition[this._position];

    const firstPart = makeImage(getBgPartImageConfig(1));
    const secondPart = makeImage(getBgPartImageConfig(1));

    firstPart.scale.set(1.235);
    firstPart.position.set(first.x, first.y);

    secondPart.scale.set(1.235);
    secondPart.position.set(second.x, second.y);

    this.addChild((this._bgFirstPart = firstPart));
    this.addChild((this._bgSecondPart = secondPart));

    if (third) {
      const thirdPart = makeImage(getBgPartImageConfig(1));

      thirdPart.scale.set(1.235);
      thirdPart.position.set(third.x, third.y);

      this.addChild((this._bgThirdPart = thirdPart));
    }
  }

  _adjustLayers() {
    this.bringToTop(this._captain);
    this.bringToTop(this._bgFirstPart);
    this.bringToTop(this._firstFiretruck);
    this.bringToTop(this._firstCharacter);
    this.bringToTop(this._bgSecondPart);
    this.bringToTop(this._secondFiretruck);
    this.bringToTop(this._secondCharacter);
    this._bgThirdPart && this.bringToTop(this._bgThirdPart);
  }
}
