/* eslint-disable class-methods-use-this */

import { fitText, makeImage, makeNinePatch, makeText } from '.';
import { Container } from './container';

export class BubbleContainer extends Container {
  constructor(config) {
    super();

    this.build(config);
  }

  build(config) {
    const { ninePatchConfig, arrowImageConfig, textConfig, angle } = config;
    this._buildArrow(arrowImageConfig, angle);
    this._buildBg(ninePatchConfig);
    const textPos = config.textPos || { x: this._bubbleBg.centerX, y: this._bubbleBg.centerY - 5 };
    this._buildText(textConfig, textPos);

    this.bringToTop(this._arrow);

    // this._drawAnchor();
  }

  _buildArrow(config, angle) {
    const { x, y } = config.anchor;

    this._arrow = makeImage(config);
    this._arrow.anchor = new Phaser.Point(x, y);
    this._arrow.angle = angle;

    this.addChild(this._arrow);
  }

  _buildBg(config) {
    this._bubbleBg = makeNinePatch(config);
    this._bubbleBg.position.copyFrom(config.position);

    this.addChild(this._bubbleBg);
  }

  _buildText(config, pos) {
    const bubbleText = makeText(config);
    bubbleText.anchor.set(0.5);
    bubbleText.position.set(this._bubbleBg.centerX, pos.y);
    fitText(bubbleText, this._bubbleBg.width * 0.9, this._bubbleBg.height * 0.9);

    this.addChild(bubbleText);
  }

  _drawAnchor() {
    // Should be on the edge of the arrow
    const gr = this.game.add.graphics();
    gr.beginFill(0xff0000, 0.5);
    gr.drawCircle(0, 0, 10);
    gr.endFill();
    this.addChild(gr);
  }
}
