/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { FiretrucksPositions } from '../../../configs/firetruck-positions-config';
import { getBackgroundImageConfig, getBgPartImageConfig } from '../../../configs/image-configs';
import { getAppearSparkleEmitterConfig } from '../../../configs/particles-configs';
import { FiretruckState } from '../../../constants';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { isSquareLikeScreen, makeEmitter, makeImage } from '../../../utils';
import { difference } from '../../../utils/array/difference';
import { Container } from '../../../utils/container';
import { FiretruckComponent } from './firetruck-component';

export class BoardView extends Container {
  constructor() {
    super();

    this._firetrucks = [];
    this._bgTopLayer = new Container();
    this._bgBottomLayer = new Container();

    this._build();

    lego.event
      .on(ModelEvents.BoardModel.FiretrucksUpdate, this._onFiretrucksUpdate, this)
      .on(ModelEvents.FiretruckModel.StateUpdate, this._onFiretruckStateUpdate, this)
      .on(ModelEvents.FiretruckModel.LevelUpdate, this._onFiretruckLevelUpdate, this)
      .on(ModelEvents.FiretruckModel.CaptainUpdate, this._onFiretruckCaptainUpdate, this);
  }

  get name() {
    return 'BoardView';
  }

  rebuild() {
    const xOffset = LP(isSquareLikeScreen() ? 625 : 825, isSquareLikeScreen() ? 500 : 475);
    const yOffset = LP(isSquareLikeScreen() ? 450 : 425, isSquareLikeScreen() ? 550 : 650);

    if (this._firetrucks.length === 1) {
      this._scaleX = this.scale.x;
      this._scaleY = this.scale.y;
      this._initialX = this.position.x;
      this._initialY = this.position.y;

      this.scale.set(isSquareLikeScreen() ? 1.5 : 2);
      this.position.set(xOffset, yOffset);
    }
  }

  getBounds() {
    return new Phaser.Rectangle(...LP([-550, -375, 1100, 800], [-350, -600, 800, 1050]));
  }

  getFiretruckPosition(uuid) {
    const firetruck = this.getFiretruckByUuid(uuid);
    return this.toGlobal(firetruck.position);
  }

  getFiretruckByUuid(uuid) {
    return this._firetrucks.find(truck => truck.uuid === uuid);
  }

  _onFiretrucksUpdate(firetrucks) {
    this._buildFiretruck(firetrucks);
    this._checkForZoom();
  }

  _onFiretruckStateUpdate(newState, previousState, uuid) {
    const firetruck = this.getFiretruckByUuid(uuid);
    switch (newState) {
      case FiretruckState.Idle:
        firetruck.enable();
        break;
      case FiretruckState.Moving:
        firetruck.disable();
        firetruck.move();
        break;
      default:
        break;
    }
  }

  _onFiretruckLevelUpdate(newLevel, previousLevel, uuid) {
    const firetruck = this.getFiretruckByUuid(uuid);
    firetruck.levelUp(newLevel);
  }

  _onFiretruckCaptainUpdate(captain, previousValue, uuid) {
    const firetruck = this.getFiretruckByUuid(uuid);
    firetruck.showCaptain();
  }

  _onFiretruckClick(uuid) {
    lego.event.emit(ViewEvents.BoardView.FiretruckClick, uuid);
  }

  _buildFiretruck(firetrucks) {
    const firetrucksModels = firetrucks.map(f => f.uuid);
    const firetrucksView = this._firetrucks.map(f => f.uuid);

    const newFiretrucks = difference(firetrucksModels, firetrucksView);

    newFiretrucks.forEach(uuid => {
      const { x, y } = FiretrucksPositions[this._firetrucks.length];
      const firetruck = new FiretruckComponent(uuid, this._firetrucks.length);

      firetruck.onClick.add(this._onFiretruckClick, this);
      firetruck.position.set(x, y);
      this._sparkleEmitter = makeEmitter(getAppearSparkleEmitterConfig(this, x + 25, y + 25));

      this._firetrucks.push(firetruck);
      this._bgBottomLayer.addChild(firetruck);
    });
  }

  _checkForZoom() {
    const xOffset = LP(isSquareLikeScreen() ? 625 : 825, isSquareLikeScreen() ? 500 : 475);
    const yOffset = LP(isSquareLikeScreen() ? 450 : 425, isSquareLikeScreen() ? 550 : 650);

    if (this._firetrucks.length === 1) {
      this._scaleX = this.scale.x;
      this._scaleY = this.scale.y;
      this._initialX = this.position.x;
      this._initialY = this.position.y;

      this.scale.set(isSquareLikeScreen() ? 1.5 : 2);
      this.position.set(xOffset, yOffset);
    } else if (this._firetrucks.length === 2) {
      this._scaleTw = this.game.add
        .tween(this.scale)
        .to({ x: this._scaleX, y: this._scaleY }, 500, Phaser.Easing.Sinusoidal.InOut, true, 0);
      this._posTw = this.game.add
        .tween(this)
        .to({ x: this._initialX, y: this._initialY }, 500, Phaser.Easing.Sinusoidal.InOut, true, 0);
    }
  }

  _build() {
    this._buildBg();
    this._buildBgLayers();
    this._buildBgParts();
  }

  _buildBg() {
    this._bg = makeImage(getBackgroundImageConfig());
    this._bg.name = 'background';
    this._bg.inputEnabled = true;

    this.addChild(this._bg);
  }

  _buildBgLayers() {
    this.addChild(this._bgBottomLayer);
    this.addChild(this._bgTopLayer);
  }

  _buildBgParts() {
    const bgTopPart = makeImage(getBgPartImageConfig(2));
    const bgWallPart = makeImage(getBgPartImageConfig(3));

    bgTopPart.scale.set(1.235);
    bgTopPart.position.set(-93.75, 62.5);

    bgWallPart.scale.set(1.235);
    bgWallPart.position.set(252.5, 215);

    this._bgTopLayer.addChild((this._bgTopPart = bgTopPart));
    this._bgTopLayer.addChild((this._bgWallPart = bgWallPart));
  }
}
