import { lego } from '@armathai/lego';
import { FiretruckState } from '../../constants';
import { firetruckIdleGuard } from '../../guards/board/firetruck-idle-gurad';
import { store } from '../../models/store';
import { setFiretruckStateCommand } from './set-firetruck-state-command';

export function onFiretruckClickCommand(uuid) {
  const firetruck = store.game.boardModel.getFiretruckByUuid(uuid);

  lego.command
    .payload(firetruck, FiretruckState.Moving)
    .guard(firetruckIdleGuard)
    .execute(setFiretruckStateCommand);
}
