import { lego } from '@armathai/lego';
import { store } from '../../models/store';
import { firetruckLevelUpCommand } from '../board/firetruck-level-up-command';
import { debitCommand } from '../game/debit-command';

export function onLevelUpClickCommand(uuid) {
  const firetruck = store.game.boardModel.getFirstFiretruckByLevel(1);
  const { cost } = store.game.optionsSetModel.getOptionByUuid(uuid);

  lego.command
    //
    .payload(firetruck)
    .execute(firetruckLevelUpCommand)

    .payload(cost)
    .execute(debitCommand);
}
